.App .content{
    max-width:1024px;
    margin: 0px 20px 0px 20px;
    padding: 20px 5px;
    margin-left:10%;
}

.section-title{
    color:var(--pop);
    display: block;
    font-size: 2em;
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
    -webkit-margin-after: 0px;
            margin-block-end: 0px;
    padding-bottom: 10px;
    font-weight: bold;
    border-bottom:1px solid #88888866;
    border-bottom:1px solid var(--light, lightgrey);
}
.content .content-section{
    padding:10px 10px;
    border-bottom: 1px solid var(--primary);
    border-bottom: 1px solid var(--accent,var(--primary));
}
.recessed-tile{
    background:url(/test/static/media/TileableWatermelon2.f70de74b.png);
    background:var(--tile-img);
    background-position:center;
    background-size:8.33%;
    box-shadow: inset 4px 4px 5px #0d1c27;
    box-shadow: inset 4px 4px 5px var(--shadow,black);
    margin-bottom:15px;
}
.recessed-tile > *{
    background:#ffffff;
    background:var(--background);
    box-shadow: 4px 4px 5px #0d1c27;
    box-shadow: 4px 4px 5px var(--shadow,black);
    margin: 5px 10px;
}
.tile-backing{
    background:#c53b3b;
}
.recessed-tile > .row {
    padding: 10px 15px;
}
footer{
    background:#1a1b17;
    background:var(--dark,black);
    color:#88888866;
    color:var(--light,white);
    padding:60px 40px;
}
.bio .pfp{
    max-width:150px;
}

@media only print{
    .App .content{
        padding: 0px;
    }
    .page-title{
        background-position:center middle;
    }
}

var(--shadow)
.hex-pie{
  margin:5px;
}
.hex-name-plate{
  font-family:sans-serif;
  height:80px;
  width:80px;
  background:white;
  background:var(--background,white);
  position:absolute;
  left: 17px;
  top: 29px;
  display:flex;
  flex-direction: column;
  text-align:center;
  justify-content: center;
  border-radius:50%;
  box-shadow:1px 2px 7px var(--shadow);
}
.hex-title{
    text-transform: capitalize;
    text-align: center;
}
.hex-info{
    margin-top:-5px;
    color: white;
    color: var(--light,white);
    font-size:80%;
    position: absolute;
    bottom: 10px;
    align-self: center;
}
.hex-hover-box{
    position: absolute;
    height: 200px;
    width: 172px;
    background: var(--dark-screen);
    color: white;
    display:flex;
    align-items:center;
    top: -200px;
}
.expandable.hex:hover{
    z-index: 1;
}
.expandable.hex:hover .hex-hover-box{
    top:0px;
}
.hex{
    margin: 5px;
}
.hex .hex-mask{
    left: 0px;
    top:0px;
}
.expandable.hex:hover .hex-mask{
    left:0px;
    top:0px;
    height: 200px;
    width: 172px;
}
.hex > .hex-mask{
    left:0px;
    right:0px;
}
.expandable.hex:hover > .hex-mask {
    left: -29px;
    top: -32px;
}
.expandable.hex:hover > .hex-mask > .hex-mask > .hex-mask  .hex-mask{
    height: 300px;
}
.expandable.hex:hover .hex-name-plate{
    left: 45px;
    top: 58px;
}


.hex-hover-box-text{
    font-size:14px;
    padding:10px;
    color:transparent;
    transition: color 0.1s linear 0.1s;
}
.expandable.hex:hover .hex-hover-box-text{
    color:white;
}

.hex-content{
  height:100%;
  width:100%;
  position:absolute;
  top:0px;
  left:0px;
  overflow-x:hidden;
  overflow-y:hidden;
  background:#54545454;
}
.hex-pie .hex-left, .hex-pie .hex-right{
  position:absolute;
  width:50%;
  height:100%;
  overflow-x:hidden;
  overflow-y:hidden;
}
.hex-pie .hex-right{
  left: 50%;
}

.hex-left .hex-fill{
  left:100%;
  top:-25%;
  transform-origin: left center;

}
.hex-right .hex-fill{
  right:150%;
  top:-25%;
  transform-origin: right center;
}
.hex-fill{
  position:relative;
  -webkit-print-color-adjust: exact;
  background-color: black !important;
  background-color: var(--primary, black) !important;
  content:" ";
  width:150%;
  height:150%;

}
.hex-danger > .hex-fill{
    background-color: black;
    background-color: var(--danger,black);
}

.hex-mask{
  transform: rotate(120deg);
  width:115px;
  height:135px;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  overflow-x:hidden;
  overflow-y:hidden;
  position:relative;
}
.hex {
    position:relative;
    height:135px;
    width:115px;
}
.hex .hex-mask ,.hex-name-plate{
    /* -webkit-transition: height 0.1s linear, width 0.1s linear,top 0.1s linear, left 0.1s linear;
    -moz-transition: height 0.1s linear, width 0.1s linear,top 0.1s linear, left 0.1s linear;
    -o-transition: height 0.1s linear, width 0.1s linear,top 0.1s linear, left 0.1s linear;
    transition: height 0.1s linear, width 0.1s linear,top 0.1s linear, left 0.1s linear; */
    transition: all 0.1s ease;
}

.hex > .hex-mask > .hex-mask > .hex-mask .hex-mask {
    height:170px;
}
.hex > .hex-mask > .hex-mask > .hex-mask > .hex-mask{
    box-shadow:inset -1px -7px 10px 0px black;
    box-shadow:inset -1px -7px 10px 0px var(--shadow,black);
}
.hex > .hex-mask > .hex-mask > .hex-mask > .hex-mask > .hex-mask{
    box-shadow: inset -6px 7px 12px 0px black;
    box-shadow: inset -6px 7px 12px 0px var(--shadow,black);
}
.hex > .hex-mask > .hex-mask > .hex-mask > .hex-mask > .hex-mask > .hex-mask{
  box-shadow: inset 3px 4px 12px 0px black;
  box-shadow: inset 3px 4px 12px 0px var(--shadow,black);
}
.hex-icon-frame{
    margin:5px;
}
.hex-icon-frame .hex-icon{
    top: 30px;
    font-size: 80px;
    position: absolute;
    color: #ffffff66;
    color: var(--light-screen, #ffffff66);
}

.hex-icon-frame .hex-content{
    background-color: black;
    background-color: var(--primary,black);
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.hex-icon-frame .hex-icon-title{
    text-shadow: 1px 1px 3px black;
    text-shadow: 1px 1px 3px var(--shadow,black);
    color: white;
    font-weight: 600;
    width: 100%;
    text-align: center;
    text-transform: capitalize;
    position: absolute;
    padding:4px;
}

.hex-row{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 15px;
    padding-top: 36px;
}
.hex-row > * {
    margin-top:-32px;
}
/*smallest screen (mobile sm) min width  2 then 1*/
@media only screen{
    .hex-row{
        width:240px;
    }
    .hex-row > .hex:nth-child(2n+1){
        margin-left:67px;
    }
}
/*mobile medium (min 375) 2 and then 1*/
@media  only screen and (min-width: 375px){

    .hex-row > .hex:nth-child(2n+1){
        margin-left:5px;/*cancel last break point*/
    }
    .hex-row{
        width:250px;
    }
    .hex-row > .hex:nth-child(3n){
        margin-left:67px;
    }
}
/*v large phones 3 and then 2? */
@media  only screen and (min-width: 475px){
    .hex-row > .hex:nth-child(3n){
        margin-left:5px;/*cancel prev*/
    }

    .hex-row{
        width:375px;
    }
    .hex-row > .hex:nth-child(5n-1){
        margin-left:67px;
    }
}

/*tablets 4 and then 3*/
@media  only screen and (min-width: 625px){
    .hex-row > .hex:nth-child(5n-1){
        margin-left:5px;/*cancel prev*/
    }

    .hex-row{
        width:500px;
    }
    .hex-row > .hex:nth-child(7n-2){
        margin-left:67px;
    }
}

/* 5 and then 4*/
@media  only screen and (min-width: 775px){
    .hex-row > .hex:nth-child(7n-2){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 630px;
    }
    .hex-row > .hex:nth-child(9n-3){
        margin-left:67px;
    }
}

/* 6 and then 5*/
@media  only screen and  (min-width: 930px){
    .hex-row > .hex:nth-child(9n-3){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 755px;
    }
    .hex-row > .hex:nth-child(11n-4){
        margin-left:67px;
    }
}

/* 7 and then 6 */
@media  only screen and (min-width: 1080px){
    .hex-row > .hex:nth-child(11n-4){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 880px;
    }
    .hex-row > .hex:nth-child(13n-5){
        margin-left:67px;
    }
}
/* 8 and then 7 */
@media only screen and (min-width: 1240px){
    .hex-row > .hex:nth-child(13n-5){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 1005px;
    }
    .hex-row > .hex:nth-child(15n-6){
        margin-left:67px;
    }
}

@media only print {
    .hex-fill{
         background-color: black !important;
         background-color: var(--primary,black) !important;
         -webkit-print-color-adjust: exact;
