    border-bottom:none;
}
.experience .location{
    grid-column:1 / span 2;
    grid-row-end: span 1;
    font-size:20px;
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    color:var(--primary);
}
.experience-location{
    grid-column:1 / span 1;
}
.experience-title{
    color:var(--dark);
    grid-column: 2 / span 1;
}
.experience-date{
    grid-column:1;
    grid-row-end:span 1;
    justify-self:center;
}
.experience-detail{
    grid-column: 1/span 2;
}

