.navbar{
    background: var(--background);
    box-shadow: 0px 3px 10px black;
    box-shadow: 0px 3px 10px var(--shadow, black);
    display:flex;
    width:100%;
    z-index:9999;
    height:63px;
}
.navbar.top{
    position:absolute;
    top:0px;
}
.navbar.sticky{
    position:-webkit-sticky;
    position:sticky;
    top:0px;
}
.navbar.fixed{
    position:fixed;
    z-index:9999!important;
    top:0px;
}
.nav-brand{
    height:100%;
    margin-left: 15px;
    align-self:center;
}
.nav-brand > * {
    height:90%;
    margin-top:auto;
    margin-bottom:auto;
}


.navbar.top .nav-button, .navbar.sticky .nav-button{
    padding:6px 12px 8px 12px;
    background:var(--background);
    color: black;
    color: var(--secondary,black);
    border: 3px solid black;
    border: 3px solid var(--secondary,black);
    font-weight: bold;
    border-left: 2px solid transparent;
    border-right: 2px solid transparent;
    display:inline-block;
    font-size: 14px;
    margin:3px 5px;

}
.navbar.top .nav-button.selected, .navbar.sticky .nav-button.selected{
    border-top-color: black;
    border-top-color: var(--primary,black);
    border-bottom-color: black;
    border-bottom-color: var(--primary,black);
    color:var(--primary);
}
.side-nav .nav-button {
    background:transparent;
    color: var(--background);
    width:100%;
    height:100%;
    border-bottom:1px solid var(--background);
}

li.nav-button{
    list-style: none;
}
.nav-button a{
    width:100%;
    height:100%;
}

ul.nav-list{
    list-style-type: none;
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
    -webkit-margin-after: 0px;
            margin-block-end: 0px;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;

}
.navbar.top , .navbar.sticky{
    transition: top linear 0.4s;
}
.navbar.top.under, .navbar.sticky.under{
    top:-65px;
}

.navbar.top .nav-list, .navbar.sticky .nav-list{
    display:flex;
    -webkit-margin-before:none;
            margin-block-start:none;
    padding: 10px 10px;
    min-height: 25px;
}

.nav-link{
    text-decoration: none;
    color: inherit;
    font-size:14px;
    font-family: var(--body-font);
}

.navbar.side-nav{
    position:fixed;
    right:0px;
    top:0px;
    bottom:0px;
    width:100px;
    z-index:9999;
}
.navbar.side-nav ul{
    grid-template-columns:100px;
    display:grid;
    width:100%;
    position:relative;
}
.navbar.side-nav .nav-brand-button{
    align-items: center;
}
.navbar.side-nav .nav-brand-button img{
    max-width: 80%;
    max-height:70%;
}
.navbar.side-nav .nav-button{
    display:flex;
    width:100%;
    height:100%;
    justify-content:center;
}
.navbar.side-nav .nav-button:hover{
    background:var(--light-screen);
}
.navbar.side-nav .nav-button a{
    display:flex;
    padding: 10px 2px;
    min-height:10px;
    /* align-items:center; */
    justify-content: center;
    text-align: center;
}
.side-nav .nav-button:last-child {
    border:none;
}
.nav-shuttle{
    width:100%;
    background:var(--shadow);
    position:absolute;
}
.nav-grid .nav-brand-button{
    grid-row-start:1;
}
.nav-grid .nav-button{
    grid-row-end: span 1;
    position:relative;
    z-index:1;
}
#light-dark-toggle{
    margin:auto;
    margin-right:20px;
}

@media only print{
    .navbar{
        display:none;
    }
}

.toggle{
    display:flex;
    align-self:center;
    justify-self: right;
}
.toggle-body{
    width:50px;
    height:20px;
    border-radius:10px;
    position:relative;
    overflow:hidden;
    /* background:var(--primary); */
    background:var(--light);

    box-shadow:inset 1px 2px 3px  black;

    box-shadow:inset 1px 2px 3px  var(--shadow, black);
}
.toggle-slider{
    position:absolute;
    background:var(--background);
    width:45%;
    height:16px;
    left:2px;
    top:2px;
    border-radius: 10px 4px 4px 10px;
    box-shadow: 1px 2px 3px black;
    box-shadow: 1px 2px 3px var(--shadow, black);
}
.toggle-body input,.toggle-body checkbox{
    position:absolute;
    left:-1000px;
}
checkbox:checked + .toggle-slider, input:checked + .toggle-slider{
    left:unset;
    right:2px;
    top:2px;
    border-radius: 4px 10px 10px 4px;
}
.toggle-label{
    margin-right:5px;
}

.logo.colored .logo-apperature{
