:root{
    --primary: #ac071c;
    --secondary: #677079;
    /* --primary: #315171;
   --secondary: #687d92; */
    /* --primary:#687d92;
    --secondary:#8c8a87; */
    --dark:#1a1b17;
    --shadow:#0d1c27;
    --light:#88888866;
    --background:#ffffff;
    --dark-screen:#000000e6;
    --light-screen:#fffffa36;
    --tile-img:url('./TileableWatermelon2.png');
    --title-img:url('./components/close-up-concrete-flora-growth.jpg');
    --body-font:'Montserrat', 'Helvetica', sans-serif;
}
