--secondary: #687d92; */
body.watermelon-light{
    --primary:#ff4a4a;
    --secondary:#4eb310;
    --dark:#0d170e;
    --shadow:#0d1c27;
    --light:#88888870;
    --background:#ffffff;
    --dark-screen:#000000e6;
    --light-screen:#fffffa36;
    --title-img:var(--primary);
    --tile-img:url('./TileableWatermelon2.png');
    --body-font:'Montserrat', 'Helvetica', sans-serif;
}
@media only screen{
    body.dark{
        /* --primary: #315171;
       --secondary: #687d92; */
       --primary: #ad2133;
       --secondary: #677079;
        /* --primary:#4b6f92;
        --secondary:#687d92; */
        --dark:#ffffff;
        --shadow:#333;
        --light:#88888866;
        --background:#1a1b17;
        --dark-screen:#000000e6;
        --light-screen:#fffffa36;
        --tile-img:url('./TileableWatermelon2.png');
        --body-font:'Montserrat', 'Helvetica', sans-serif;
    }
    body.watermelon-dark{
        --primary:#ff4a4a;
        --secondary:#4eb310;
        --dark:#ffffff;
        --shadow:#333;
        --light:#88888870;
        --background:#212322;
        --dark-screen:#000000e6;
        --light-screen:#fffffa36;
        --tile-img:url('./TileableWatermelon2.png');
        --body-font:'Montserrat', 'Helvetica', sans-serif;
    }
}
