body{
    color:var(--dark);
    background:var(--background);
}
a{
    color:var(--accent,var(--primary));
}


.App .content{
    max-width:1024px;
    margin: 0px 20px 0px 20px;
    padding: 20px 5px;
    margin-left:10%;
}

.section-title{
    color:var(--pop);
    display: block;
    font-size: 2em;
    margin-block-start: 0px;
    margin-block-end: 0px;
    padding-bottom: 10px;
    font-weight: bold;
    border-bottom:1px solid var(--light, lightgrey);
}
.content .content-section{
    padding:10px 10px;
    border-bottom: 1px solid var(--accent,var(--primary));
}
.recessed-tile{
    background:var(--tile-img);
    background-position:center;
    background-size:8.33%;
    box-shadow: inset 4px 4px 5px var(--shadow,black);
    margin-bottom:15px;
}
.recessed-tile > *{
    background:var(--background);
    box-shadow: 4px 4px 5px var(--shadow,black);
    margin: 5px 10px;
}
.tile-backing{
    background:#c53b3b;
}
.recessed-tile > .row {
    padding: 10px 15px;
}
footer{
    background:var(--dark,black);
    color:var(--light,white);
    padding:60px 40px;
}
.bio .pfp{
    max-width:150px;
}

@media only print{
    .App .content{
        padding: 0px;
    }
    .page-title{
        background-position:center middle;
    }
}
