.page-title  > * {
    text-align: center;
    font-size:100px;
    font-family: 'Red Hat Text', sans-serif;
    font-weight: 400;
    text-shadow: 2px 3px 4px var(--shadow)
}
.page-title .first-name{
    font-size: 62px;
    display: block;
    margin-bottom: -22px;
    line-height: 47px;
    position: relative;
    /* z-index: 1; */
}
.page-title .preferred-name{
    font-family: 'Princess Sofia', cursive;
    font-size: 112px;
    margin-right: -15px;
    line-height: 100px;
    margin-top: -15px;
    text-shadow:none;
}
#title{

}
#title{
    width:100%;
    height:calc(100vh - 63px);
    background:var(--primary);
    background:var(--title-img);
    background-position:center;
    background-size:cover;
    color:var(--background);
    display:flex;
    justify-content: center;
    align-items: center;
    position:relative;
}
#title .contact-frame{
    position:absolute;
    bottom:10px;
    right:10px;
}
.indentity{
    display:block;
}

#title .logo{
    max-height:70%;
    max-width:80%;
    width:80%;
    height:80%;
}
.title-mask{
    background:#ac071c59;
    width:100%;
    height:100%;
    display:flex;
    justify-content: center;
    align-items: center;
}
