.contact-card a,.contact-card button{
    font-size: 40px;
    display: inline-block;
    border-radius: 50%;
    margin: 5px;
    line-height: 1.33em;
    height: 1.33em;
    width: 1.33em;
    text-align: center;
    color:var(--primary,white);
    background: transparent;
    border:none;
}
.contact-card a.toggled,.contact-card button.toggled{
    background:var(--primary);
    color:var(--background);
}
.contact-card{
    padding:8px;
    min-width:250px;
    border: 2px solid var(--background);
    display:flex;
}
.contact-frame{
    min-width:250px;
    position:relative;
}
.contact-frame > .contact-card{
    position:relative;
    background:var(--primary);
    z-index:1;
}
.contact-frame > .contact-panel{
    position:absolute;
    top:10px;
    right:0px;
    bottom:0px;
    left:0px;
    transition: top 0.4s linear;
    text-align:center;
    padding:5px;
    color:var(--primary);
}
.contact-frame > .contact-card,.contact-frame > .contact-panel{
    border-radius:8px;
    background:var(--background);
    border: 2px solid var(--primary);

}

.contact-panel.revealed{
    top:-30px;
}
.contact-panel a{
    /* width: 100%; */
    display:block;
    text-align:center;
    color:var(--primary);
    /* color:var(--primary); */
    text-decoration: none;
}
