li.experience-item{
    list-style: none;
}
.print ul.experience-items{
    margin-block-start: 0em;
    margin-block-end: 0em;
    padding-inline-start:3em;
}
.print li.experience-item::before{
    content: "\2b21";
    color: var(--dark);
    display: inline-block;
    width: 1.5em;
    margin-left: -1.5em;
}
.web li.experience-item{
    padding-bottom: 5px;
}
.web ul.experience-items{
    margin-block-start: 0em;
    margin-block-end: 0em;
    padding-inline-start:0em;
}
.printable.experience.row{
    padding: 20px 10px;
    border-top: 1px solid var(--light);
    display:grid;
    grid-template-columns: 155px auto;
    grid-template-rows:auto;
    grid-gap: 10px;
    grid-auto-flow: row;
}
.printable .experience-placement.row:last-child{
    border-bottom:none;
}
.experience .location{
    grid-column:1 / span 2;
    grid-row-end: span 1;
    font-size:20px;
    margin-block-start: 0em;
    margin-block-end: 0em;
    color:var(--primary);
}
.experience-location{
    grid-column:1 / span 1;
}
.experience-title{
    color:var(--dark);
    grid-column: 2 / span 1;
}
.experience-date{
    grid-column:1;
    grid-row-end:span 1;
    justify-self:center;
}
.experience-detail{
    grid-column: 1/span 2;
}

.experience-set.web{
    /* background:var(--primary,white);
    box-shadow: inset 4px 4px 5px var(--shadow,black); */
    margin-bottom:15px;
    display:block;
    /* padding:20px 10px; */
}
.experience-set.web > *{
    background:var(--background);
    /* box-shadow: 4px 4px 5px var(--shadow,black); */
    /* margin: 5px 10px; */
    min-width: 300px;
    display:block;
    /* padding: 20px; */
}
.experience .location{
    color:var(--primary)
}
.web .experience .location{
    margin-block-start: 0.5em;
    margin-block-end: 0.5em;
    font-size: 20px;
}
.web .experience-date{
    padding-bottom: 12px;
    text-align: center;
    display:flex;
    min-width:152px;
    width: 13%;
    border-right: 1px solid var(--light,lightgrey);
}
.web .experience-title{
    margin-bottom:8px;
    display:flex;
}
.experience{
    padding:10px;
}
.experience-position{
    display:flex;
    margin: 0px -10px;

}
.experience-position > *{
    padding: 5px 10px;
}
.experience-period{
    padding:10px 0px;
}
.experience-set.brief.web .experience-date{
    border-bottom:none;
    padding-bottom: 0px;
}
